.wrapp {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
  align-items: center;
  -ms-flex-pack: center;
  justify-content: center;
}
.loader {
  position: relative;
}
.bag {
  position: relative;
  z-index: 2;
}
.bag svg {
  width: 130px;
}
.coin {
  display: inline-block;
  position: absolute;
  -webkit-transform: translateY(-80px);
  -ms-transform: translateY(-80px);
  transform: translateY(-80px);
  opacity: 0;
  left: 0;
  right: 0;
  margin: auto;
  width: 50px;
  -webkit-transition: opacity 0s, -webkit-transform 1s;
  transition: opacity 0s, -webkit-transform 1s;
  -o-transition: transform 1s, opacity 0s;
  transition: transform 1s, opacity 0s;
  transition: transform 1s, opacity 0s, -webkit-transform 1s;
}
.coin svg {
  width: 100%;
}
.coin.active {
  opacity: 1;
  -webkit-transform: translateY(10px);
  -ms-transform: translateY(10px);
  transform: translateY(10px);
}



