.btn {
  background: none;
  border: none;
  cursor: pointer;
  display: none;
}
.btn:active,
.btn:focus {
  outline: none;
}
.items {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
  justify-content: space-between;
}
.items:hover .btn {
  display: initial;
}
.input {
  border: none;
  display: inline-block;
  max-width: 70px;
  padding: 5px 10px;
  background: rgba(33, 150, 243, 0.3);
}
.input:active,
.input:focus {
  outline: none;
}



