.input {
  border: none;
  border-bottom: 1px solid #000;
  padding: 5px;
  margin: 5px 0;
  width: 100%;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}
.input:focus,
.input:active {
  outline: none;
}
.form {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}
.form button {
  width: 100%;
}



